import { Pagination } from 'antd';
import { getPostAll } from 'api/home';
import Carouse from 'components/Carousel';
import HomeItem from 'components/HomeItem';
import Loading from 'components/loading';
import { isEmpty } from 'lodash';
import React, { useState } from 'react';
import { useQuery } from 'react-query';
import { IPost } from 'types/managerType';
import styles from './style.module.scss';
const initPage = {
  page: 1,
};
function Home() {
  const [page, setPage] = useState(initPage);
  const { data: listPostAll, isFetching: isFetchingPostAll } = useQuery(['listPostAll', page], () => getPostAll(page));

  if (isFetchingPostAll) {
    return <Loading />;
  }
  const handleChangePage = (page: number, pageSize: number) => {
    const newPage = {
      page,
    };
    setPage(newPage);
  };
  // ChannelService.boot({
  //   pluginKey: 'df80cad3-782a-4fb5-a9fc-7813cc69017b', //please fill with your plugin key
  // });

  return (
    <div className={styles.container}>
      <div className={styles.homeContainer}>
        <div className={styles.carousel}>
          <Carouse />
        </div>
        <div className={styles.articleContainer}>
          <div className={styles.listArticle}>
            {listPostAll?.listPost?.map((item: IPost) => (
              <HomeItem item={item} key={item?.id} />
            ))}
          </div>
        </div>
        <div className={styles.pagination}>
          {isEmpty(listPostAll?.listPost) ? null : (
            <Pagination current={page.page} total={listPostAll.total} onChange={handleChangePage} pageSize={10} />
          )}
        </div>
      </div>
    </div>
  );
}

export default Home;
