import { EyeOutlined } from '@ant-design/icons';
import { Button } from 'antd';
import React from 'react';
import { NavLink, useHistory } from 'react-router-dom';
import { IPost } from 'types/managerType';
import styles from './style.module.scss';
import View from './View';

interface HomeItemProps {
  item: IPost;
}
function HomeItem(props: HomeItemProps) {
  const history = useHistory();
  const { item } = props;
  return (
    <div className={styles.articleItem}>
      <div className={styles.postImg}>
        <NavLink to={`/post/${item.id}`} className={styles.navLink}>
          <img src={item.thumbnail} alt="" />
        </NavLink>
      </div>
      <div className={styles.postContent}>
        <div className={styles.title}>
          <NavLink to={`/post/${item.id}`} className={styles.navLinkContent}>
            <h3> {item.title} </h3>
          </NavLink>
          <View icon={<EyeOutlined />} view={item?.view} />
        </div>
        <div className={styles.content}>
          <p>{item.summary}</p>
        </div>
        <Button className={styles.more} onClick={() => history.push(`/post/${item.id}`)}>
          Xem ThÃªm
        </Button>
      </div>
    </div>
  );
}

export default HomeItem;
